<template>
    <div>
        <v-parallax src="@/assets/main.jpg" height="800">
            <div class="v-parallax-div">
                <div class="v-parallax-div1">New CLOTHING Arrivals</div>
                <div class="v-parallax-div2">SHOP NOW</div>
            </div>
        </v-parallax>

        <!-- 새 상품 슬라이드 -->
        <Slider :clothing="clothing" />
        <!-- 새 상품 슬라이드 끝 -->

        <br>

        <!-- 카테고리 슬라이드 -->
        <v-carousel
            hide-delimiters
            cycle
            height="600"
        >
            <v-carousel-item
                v-for="(image, i) in slide_img"
                :key="i"
            >
                <v-sheet
                    height="100%"
                >
                    <div class="wrap">
                        <v-img class="v-carousel-img" :src="image" />
                        <div class="category-slide-text">
                            <div class="category-slide-text-div1">{{slide_text[i]}}</div>
                            <div class="category-slide-text-div2">SHOP NOW</div>
                        </div>
                    </div>
                </v-sheet>
            </v-carousel-item>
        </v-carousel>
        <!-- 카테고리 슬라이드 끝 -->

        <br>
        
        <!-- 인기 상품 슬라이드 -->
        <div class="mini-title-div">MOST POPULAR</div>
        <br>
        <v-img src="@/assets/most_popular.jpg" />
        <!-- 인기 상품 슬라이드 끝 -->

    </div>
</template>

<script>
    export default {
        data() {
            return {
                slide_img: [
                    require('@/assets/tops.jpg'),
                    require('@/assets/bottoms.jpg'),
                    require('@/assets/footwear.jpg'),
                    require('@/assets/accessories.jpg')
                ],
                slide_text: [
                    "TOPS",
                    "BOTTOMS",
                    "FOOTWEAR",
                    "ACCESSORIES"
                ],
                clothing: [],
            }
        },

        created() {
            this.getData();
        },

        methods: {
            getData() {
                this.$axios.get('/test').then(res => {
                    res.data.forEach(element => {
                        this.clothing.push({
                            name: element.name,
                            price: element.price.toLocaleString('ko-KR'),
                            url: element.url
                        });
                    });
                })
            }
        },
    };
</script>

<style>
    .v-parallax-div {
        position: relative;
        bottom: -200px;
        text-align: center;
    }

    .v-parallax-div1 {
        font-size: 34px;
        line-height: 47.6px;
    }

    .v-parallax-div2 {
        font-size: 21px;
        font-weight: 700;
        letter-spacing: 0.21px;
        line-height: 27.3px;
    }

    .mini-title-div {
        font-size: 17px;
        letter-spacing: 1.7px;
        text-align: center;
        padding-top: 30px;
    }

    .slide-product-name {
        font-size: 17px;
        line-height: 23.8px;
        margin-top: 5px;
        display: block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .slide-product-price {
        font-size: 16px;
        line-height: 16px;
        color: #666666;
        margin-top: 5px;
    }

    .wrap {
        position: relative;
    }

    .v-carousel-img {
        filter: brightness(40%);
    }

    .category-slide-text {
        position: absolute;
        top: 250px;
        width: 100%;
        text-align: center;
        color: #fff;
        
    }

    .category-slide-text-div1 {
        font-size: 20px;
        font-weight: 300;
        letter-spacing: 2px;
    }

    .category-slide-text-div2 {
        font-size: 15px;
        font-weight: 100;
        margin-top: 10px;
        letter-spacing: 7px;
    }
</style>